html {
  background-color: white;
  color: #363535;
  text-align: center;
  overflow-x: hidden;

}
body{
  scroll-behavior: smooth;
}

/* scroll bar */
::-webkit-scrollbar {
    width: 0.6rem;
    height: 0.6rem;
}

::-webkit-scrollbar-track-piece {
    background-color: #eee;
}

::-webkit-scrollbar-thumb:vertical {
    height: 5rem;
    background-color: #454545;
}

/* intro page */
.intro {
  height: 100vh;
  height: -webkit-fill-available;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  color: #363535;
  font-weight: bold;
  font-size: 6vw;
  font-family: Roboto, sans-serif;
}

.scroll {
  font-size: 3vw;
  position: absolute;
  bottom: 2vw;
}

.scroll:hover {
  cursor: pointer;
  font-size: 3.5vw;
}

/* input page */
.input-page {
  height: 100vh;
  height: -webkit-fill-available;
  width: 100vw;
  background-color: #363535;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.next {
  font-size: 3vw;
  bottom: 2vw;
  color: #454545;
  position: absolute;
  background-color: transparent;
  border:none;
}
.next:hover {
  font-size: 3.5vw;
  cursor: pointer;
}

.white {
  color: white;
}
.grey {
  color: grey;
}

/*results groups page*/
.container {
  display: grid;
  grid-template-rows: auto auto auto;
  grid-template-columns: auto auto auto;
  align-items: start;
  text-align: center;
  margin-bottom: 6vw;
  margin-top: 5vw;
  width: 80vw;
  margin-left: 10vw;
}
.results-title {
  margin-left: 10vw;
  text-align:left;
  color:white;
  font-weight: bold;
  font-size: 2vw;
}
.disclaimer {
  color: #adadad;
  font-size: 0.6vw;
  margin-left: 10vw;
  text-align:left;
}
.results-icons {
  display: block;
  margin-bottom: 1vw;
  cursor: pointer;
}
.results-icons-text {
  text-align: left;
  font-size: 0.8vw;
  color:white;
  cursor: pointer;
}
.inactive{
  color: grey;
}

/* results pages */
.planet-results {
  height: 100vh;
  height: -webkit-fill-available;
  position: relative;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 25vw auto 25vw;
  align-items: center;
  justify-items: center;
}

.info-field {
  display: grid;
  grid-column: 2/3;
  grid-template-rows: 33% 20% 33%;
  align-items: center;
  justify-items: center;
}

.icons {
  font-size: 9vw;
  grid-row: 1/2;
}

.sign-house {
  font-size: 1.5vw;
  grid-row: 2/3;
}
.description {
  font-size: 1.1vw;
  grid-row: 3/3;
  text-align: left;
}

/*mobile phones*/
@media screen and (max-width: 539px) {
  .title {
    font-size: 7vw;
  }
  .scroll {
    font-size: 7vw;
    bottom: 3vw;
  }
  .scroll:hover {
    font-size: 7.5vw;
  }
  .next {
    font-size: 7vw;
    bottom: 4vw;
  }
  .next:hover {
    font-size: 7.5vw;
  }
  .container {
    grid-template-rows: auto auto auto;
    grid-template-columns: auto auto;
    margin: 10vw 10vw 10vw;
    width: 80vw;
  }
  .results-title {
    font-size: 3.5vw;
    margin-left: 10vw;
    width: 80vw;
  }
  .disclaimer {
    font-size: 2vw;
    width: 80vw;
  }
  .results-icons {
    margin-bottom: 1vw;
  }
  .results-icons-text {
    font-size: 3vw;
  }
  .inactive{
    color: grey;
  }
  .planet-results {
    height: 100vh;
    height: -webkit-fill-available;
    grid-template-columns: 20vw auto 20vw;
  }
  .info-field {
    grid-template-rows: 20% 20% 33%;
  }
  .icons {
    font-size: 30vw;
    margin-top: 10vh;
  }
  .sign-house {
    font-size: 7vw;
  }
  .description {
    font-size: 4vw;
  }
}

/*tablets*/
@media screen and (min-width: 540px) and (max-width: 1024px){
  .title {
    font-size: 7vw;
  }
  .scroll {
    font-size: 6vw;
    bottom: 3vw;
  }
  .scroll:hover {
    font-size: 6.5vw;
  }
  .next {
    font-size: 5vw;
    bottom: 4vw;
  }
  .next:hover {
    font-size: 5.5vw;
  }
  .container {
    grid-template-rows: auto auto auto auto;
    grid-template-columns: auto auto;
    margin: 10vw;
    width: 80vw;
  }
  .results-title {
    font-size: 3.5vw;
    margin-left: 10vw;
    width: 80vw;
  }
  .disclaimer {
    font-size: 2vw;
    width: 80vw;
  }
  .results-icons {
    margin-bottom: 2vw;
  }
  .results-icons-text {
    font-size: 2vw;
  }
  .inactive{
    color: grey;
  }
  .planet-results {
    height: 100vh;
    height: -webkit-fill-available;
    grid-template-columns: 20vw auto 20vw;
  }
  .info-field {
    grid-template-rows: 20% 20% 33%;
  }
  .icons {
    font-size: 20vw;
    margin-top: 10vh;
  }
  .sign-house {
    font-size: 5vw;
  }
  .description {
    font-size: 3vw;
  }
}